.about {
  padding: var(--py) var(--px);
  display: grid;
  grid-template-columns: 1fr;
  gap: 60px;
}

.about-left {
  position: relative;
}

.about-left .img-box {
  aspect-ratio: 1 / 1;
  overflow: hidden;
}

.about-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  animation: lazy-scaleUp 30s linear infinite;
}

@keyframes lazy-scaleUp {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

.abs-content-box {
  background: var(--white);
  position: absolute;
  bottom: -20px;
  right: -20px;
  padding: 30px;
  text-align: center;
  box-shadow: 2px 2px 10px hsla(0, 0%, 0%, 0.1);
}

.dotted-border {
  border: 3px dotted var(--cultured);
  padding: 10px;
}

.number-lg {
  font-size: var(--fs-1);
  font-family: 'Monoton', cursive;
  color: var(--space-cadet);
}

.text-md {
  font-size: var(--fs-3);
  font-weight: 600;
  color: var(--space-cadet);
}

.shape-6 {
  right: -9px;
  top: 35%;
  animation: scaleUp 2s 3s ease-in-out infinite;
}

.shape-7 {
  top: 50%;
  left: -15px;
  animation: scaleUp 3s ease-in-out infinite;
}

.shape-8 {
  top: -10px;
  left: 15%;
  animation: moveUp 3s 1s ease-in-out infinite;
}

.shape-9 {
  bottom: -20px;
  left: 5%;
  animation: moveUp 3s ease-in-out infinite;
}

.about-right .section-title {
  margin-bottom: 30px;
  text-align: start;
}

.about-right .section-text {
  text-align: start;
}

.about-right p {
  max-width: 100%;
  margin-bottom: 30px;
}

@media (min-width: 570px) {
  .about {
    max-width: var(--mw);
    margin-inline: auto;
  }
}

@media (min-width: 768px) {
  .about-left .img-box {
    aspect-ratio: 5 / 3;
  }
}

@media (min-width: 1024px) {
  .about {
    grid-template-columns: 3fr 4fr;
    gap: 80px;
    align-items: center;
    padding: var(--py) var(px) 120px;
  }

  .about-left .img-box {
    aspect-ratio: auto;
    object-fit: cover;
  }

  .shape-7 {
    top: 80%;
    left: -25px;
    width: 50px;
  }

  .shape-9 {
    bottom: -40px;
    left: 15%;
    width: 100px;
  }
}
